@use 'base/variables' as v;
@use 'base/mixins' as m;

.form {
	&__grid {
		@include m.tablet {
			@include m.grid(2, v.$separation);
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		background-color: v.$primary;
		padding: 5rem;
	}

	&__heading {
		color: v.$white;
		text-align: center;
	}

	&__field {
		margin-bottom: 1rem;
	}

	&__label {
		display: block;
		text-transform: uppercase;
		color: v.$white;
		font-weight: 700;
		margin: 1rem 0;
	}

	&__input {
		width: 100%;
		padding: 1.5rem;
		border: none;
	}

	&__submit {
		margin-top: 2rem;
		@include m.button(v.$secondary, v.$white);
		@include m.tablet {
			width: 100%;
		}
	}

	&__image-container {
		margin-top: v.$separation;
		display: flex;
		justify-content: center;
	}

	&__img {
		width: 30rem;

		@include m.desktop {
			width: 100%;
		}
	}
}
